.App{
    
}

.info{
    width: 100%;
    height: 100vh;

    box-sizing: border-box;
    padding: 20px;

    overflow: auto;

    border-left: 1px solid #dedede;

    h2{
        font-weight: bold;

        span{
            background: -webkit-linear-gradient(-70deg,#f25050,#0a66d1);
            background-clip: text;
            color: transparent;
            display: block;
            margin-top: -7px;
        }
    }

    &-input{
        &-liquid-wrapper{
            width: 100%;
            
            margin-top: 30px;
            margin-bottom: 30px;
        }

        &-liquid-item{
            width: 100%;
            height: 33px;
            border: 1px solid #dedede;
            border-radius: 2px;

            margin-bottom: 5px;

            display: flex;

            align-items: center;
            justify-content: space-between;

            cursor: pointer;

            transition: all 0.3s ease;

            &.selected{
                background: #f3f3f3;
            }

            &:hover{
                background: #f3f3f3;
            }

            &__name{
                font-size: 15px;
                font-weight: bold;
                text-transform: uppercase;
                padding: 0 0 0 15px;
            }

            &__rho{
                margin-right: 22px;
                font-size: 15px;

                span{
                    letter-spacing: -2px;

                    span{
                        vertical-align: super;
                        font-size: 11px;
                    }
                }
            }
        }

        &-name{
            font-size: 21px;
            margin-bottom: 6px;
            color: #333;

            span{
                vertical-align: super;
                margin-left: 6px;
                font-size: 22px;
                color: #a5a5a5;
            }
        }

        .form-group{
            padding-bottom: 10px;
            margin-bottom: 20px;
        }

        .form-group:nth-child(1){
            border-bottom: 1px solid #dedede;
        }
    }
}